import React from "react";
import Gif from "./HealthGuardian - GIF.gif";
import Mocap from "./Mocap.png";
import Finger from "./Finger.png";
import Keep from "./KeepSafe.png";
import Cell from "./Cellphone.png";

function HealthGuardian() {
  return (
    <div>
      <div className="ParteUm">
        <img className="Gif" src={Gif} alt="Gif" />
        <div className="TextoParteUm">
          <div className="Titulo_HealthGuardian">HealthGuardian</div>
          <div className="Subtitulo_HealthGuardian">
            Moldando o futuro da sade!
          </div>
          <div className="Texto_HealthGuardian">
            Apresentamos o HealthGuardian, uma inteligncia artificial que
            monitora continuamente sua sade ps-atendimento. Dependendo da
            gravidade dos sintomas, faz perguntas para avaliar a necessidade de
            retorno ao mdico, proporcionando orientaes personalizadas para o
            seu bem-estar contnuo. Atua como seu cuidador virtual,
            proporcionando monitoramento inteligente e suporte remoto.
          </div>
        </div>
      </div>
      <div className="Tudo">
        <div className="ParteDois">
          <div className="funcionamento">
            <strong>Como funciona?</strong>
          </div>
          <div className="Cards">
            <div className="Card">
              <div className="CardTitulo">
                <strong>Monitoramento personalizado</strong>
              </div>
              <div className="CardTexto">
                Aps um procedimento mdico, seu mdico determinar a
                necessidade de acompanhamento. O HealthGuardian utilizar
                tecnologia avanada para monitorar seus sinais vitais e sintomas
                de maneira contnua.
              </div>
            </div>
            <div className="Card">
              <div className="CardTitulo">
                <strong>Avaliaes dirias do paciente</strong>
              </div>
              <div className="CardTexto">
                Dependendo da gravidade do procedimento, o HealthGuardian
                solicitar que voc responda a perguntas dirias sobre seu
                estado de sade. Essas avaliaes so personalizadas e adaptadas
                s orientaes especficas do seu mdico.
              </div>
            </div>
            <div className="Card">
              <div className="CardTitulo">
                <strong>Alertas e intervenes precoces</strong>
              </div>
              <div className="CardTexto">
                Caso o sistema identifique alguma alterao preocupante em seus
                dados ou nas respostas fornecidas, alertas proativos sero
                acionados. Se necessrio, o HealthGuardian facilitar a
                comunicao direta com seus profissionais de sade, permitindo
                intervenes rpidas.
              </div>
            </div>
            <div className="Card">
              <div className="CardTitulo">
                <strong>Tempo de monitoramento</strong>
              </div>
              <div className="CardTexto">
                O perodo de monitoramento dirio ser estipulado pelo mdico,
                levando em considerao a natureza do procedimento e sua
                gravidade. Ao final desse perodo, uma avaliao geral ser
                realizada para determinar se  necessrio continuar o
                monitoramento.
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="ParteTres">
        <h1 className="Texto_ParteTres">
          Experimente o futuro do cuidado ps-hospitalar com o HealthGuardian
        </h1>
        <img className="Mocap" src={Mocap} alt="Mocap" />
      </div>
      <div className="Tudo_ParteQuatro">
      <div className="ParteQuatro">
        <div className="Main_ParteQuatro">
        <h1 className="TituloParteQuatro">Nossos benefcios</h1>
        <div className="Topicos_ParteQuatro">
          <div className="Sub_ParteQuatro">
            <img className="Imagem_ParteQuatro" src={Finger} alt="Finger" />
            <h2 className="Texto_Imagem_ParteQuatro">Recuperao personalizada</h2>
          </div>
          <div className="Sub_ParteQuatro">
            <img className="Imagem_ParteQuatro" src={Keep} alt="Keep" />
            <h2 className="Texto_Imagem_ParteQuatro">Preveno de complicaes</h2>
          </div>
          <div className="Sub_ParteQuatro">
            <img className="Imagem_ParteQuatro" src={Cell} alt="Cell" />
            <h2 className="Texto_Imagem_ParteQuatro">Comunicao eficiente</h2>
          </div>
        </div>
        </div>
      </div>
      </div>
    </div>
  );
}

export default HealthGuardian;
