import React from 'react';
import Insta from "./Social Media Icon Square/Instagram.png";
import Linkedin from "./Social Media Icon Square/LinkedIn.png";
import Lu_Ju from "./Lu_ju.png";



function Criadores() {


  return (
    <main className="Criadores">
          <div className='Titulos'>
          <h1 className='Titulo_Criadores'>Conhea nosso time</h1>
          <h2 className='Subtitulo_Criadores'>Descubra a dupla talentosa por trs do sistema HealthGuardian
            que est empenhada em moldar o futuro da sade!</h2>
        </div>
      <div className='Criadores_Main'>
        <img className='Ju-Lu' src={Lu_Ju} alt="Lu_Ju" />
        <div className='Cards_Criadores'>
          <div className='Card_Luis'>
            <div className='Parte1_Card_Luis'>
              <h2>Luis Barreto</h2>
              <p>19 anos</p>
              <div className='Insta_Linkedin'>
                <a href="https://www.instagram.com/lbarreto_351/" target="_blank" rel="noopener noreferrer">
                <img className="Insta" src={Insta} alt="Insta" />
                </a>
                <a href="https://www.linkedin.com/in/lu%C3%ADs-barreto1351/" target="_blank" rel="noopener noreferrer">
                <img className="Linkedin" src={Linkedin} alt="Linkedin" />
                </a>
              </div>
            </div>
            <div className='Parte2_Card_Luis'>
              <p>Com 19 anos, estudo engenharia de software e estou entusiasmado com a
                oportunidade de aprender e criar no vasto mundo da tecnologia.
                Tenho determinao para superar desafios e estou ansioso para o futuro.
              </p>
            </div>
          </div>
          <div className='Card_Luis'>
            <div className='Parte1_Card_Luis'>
              <h2>Julia Lins</h2>
              <p>20 anos</p>
              <div className='Insta_Linkedin'>
                <a href="https://www.instagram.com/Juh.az/" target="_blank" rel="noopener noreferrer">
                <img className="Insta" src={Insta} alt="Insta" />
                </a>
                <a href="https://www.linkedin.com/in/julia-azevedo-lins/" target="_blank" rel="noopener noreferrer">
                <img className="Linkedin" src={Linkedin} alt="Linkedin" />
                </a>
              </div>
            </div>
            <div className='Parte2_Card_Luis'>
              <p>Estudante de Engenharia de Software na FIAP e Estagiria de Perfomance BI no C6 Bank. 
                Minha paixo envolve dados, tecnologia, criatividade e inovao.
              </p>
            </div>
          </div>
        </div>
    </div>
    </main>
  );
}

export default Criadores;